<template>
  <div class="message-detail-container">
    <div class="head-box">
      <div class="img-box">
        <img v-if="fromHeadPicId" v-lazy="fromHeadPicId" alt="" class="img" />
        <img v-else src="/static/img/logo.png" alt="" class="img" />
      </div>
    </div>

    <div class="right-con">
      <p class="name-box"><span class="name">{{fromName}}</span><span class="official" v-if="fromMemberId == 0">官方</span></p>
      <ul class="detail-list" v-for="(item, index) in list" :key="index">
        <li class="detail-item">
          <div class="message">
            {{item.message}}
            <p class="time">{{item.createTime | formatTime}}</p>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import Storage from '@/utils/Storage'
export default {
  name: 'MessageDetail',
  data () {
    return {
      list: [],
      unreadCount: 0,
      fromMemberId: this.$route.params.id,
      fromName: '',
      fromHeadPicId: 0
    }
  },
  computed: {
    ...mapState({
      userData: 'userData'
    }),
    ...mapGetters({
      userId: 'userId'
    })
  },
  methods: {
  },
  mounted () {
    this.$root.openLoading()
    this.$http('/api/member/message/list', {
      fromMemberId: this.fromMemberId
    }, 'get').then(res => {
      this.$root.closeLoading()
      if (res.code === '0000') {
        this.list = res.data.data
        if (this.list.length > 0) {
          this.fromName = this.list[0].name || 'Hello部落'
          this.fromHeadPicId = this.list[0].headPicUrl
        }
      } else {
        this.$toast(res.msg)
      }
    }, err => {
      this.$root.closeLoading()
      this.$toast(err)
    })
  }
}
</script>

