<template>
  <div class="user-info-component">
    <div class="img-swiper" v-if="info.coverPicUrlList">
      <Swipe :autoplay="0" indicator-color="white" class="img-box" ref="swipe">
        <SwipeItem class="img-box" v-for="(img, index) in info.coverPicUrlList" :key="img" @click="showPreview(index)">
          <img class="img" :src="`${img}`" />
        </SwipeItem>
      </Swipe>
      <span class="img-num">{{info.coverPicList.length}}</span>
    </div>
    <div class="info-box" v-if="info.id">
      <div class="name-op-con">
        <span class="name-con"><i class="sex-icon" :class="{female: info.sex == 2}"></i><span
            class="nick">{{info.nickName}}</span><img class="identify-icon" src="~@/assets/img/identify.png"
            v-if="info.identityAuth === 3 && info.academicAuth === 3 && info.workAuth === 3" /></span>
        <span class="op-con" v-if="mId != info.mid">
          <a href="javascript:;" class="report btn" @click="switchReport">举报</a>
          <a href="javascript:;" class="follow btn" :class="{'followed': info.follow}"
            @click="switchFollow">{{info.follow ? '取消关注' : '关注'}}</a>
        </span>
      </div>
      <div class="id-con" ref="code-con"><strong class="id">ID: {{info.mid}}</strong><a href="javascript:;" v-if="$route.name === 'PersonInfo'"
          class="copy-btn" @click="execCopy(info.mid)">复制</a></div>
      <p class="info-con">
        {{info.age}}岁&nbsp;（{{calculater(info.birthday.split('-')[0])}}）&nbsp;{{info.high}}cm&nbsp;{{constellationMap[info.constellation]}}
      </p>

      <p class="info-con">
        {{educationMap[info.education]}}
        {{info.ifStudent ? '在读' : ''}}{{info.school ? `（${info.school}）` : ''}}
      </p>
      <template v-if="!info.ifStudent">

        <p class="info-con desc">
          公司：{{info.companyVisible && info.company ? `${info.company}` : '保密'}}
          <span class="info-con desc" v-if="!info.companyVisible">
            {{info.industry ? `(${info.industry})` : ''}}
          </span>
        </p>
        <p class="info-con desc">
          职位：{{info.job}}
        </p>
      </template>
      <p class="info-con">{{statusMap[info.singleStatus]}} {{assetsMap[info.richesInfo]}}
        {{info.annualSalaryVisible && info.annualSalary != 0 ? salaryMap[info.annualSalary] : ''}}</p>
      <p class="info-con location">
        {{cityText(info)}}
        ·
        {{nativeCityText(info)}}
      </p>
    </div>

    <div class="desc-list" v-if="info.mid">
      <div class="desc-item">
        <p class="desc-top"><img src="~@/assets/img/self-description.png" alt="" class="self-description" />自我描述</p>
        <p class="desc-detail">{{info.selfDescription}}</p>
      </div>
      <div class="desc-item">
        <p class="desc-top"><img src="~@/assets/img/family.png" alt="" class="self-description" />家庭背景</p>
        <p class="desc-detail">{{info.familyInfo}}</p>
      </div>
      <div class="desc-item">
        <p class="desc-top"><img src="~@/assets/img/experice.png" alt="" class="self-description" />个人经历</p>
        <p class="desc-detail">{{info.personalExperience}}</p>
      </div>
      <div class="desc-item">
        <p class="desc-top"><img src="~@/assets/img/hobby.png" alt="" class="self-description" />兴趣爱好</p>
        <p class="desc-detail">{{info.hobby}}</p>
      </div>
      <!-- <div class="desc-item">
        <p class="desc-top"><img src="~@/assets/img/economic.png" alt="" class="self-description" />经济能力</p>
        <p class="desc-detail">{{info.richesInfo}}</p>
      </div> -->
      <div class="desc-item">
        <p class="desc-top"><img src="~@/assets/img/love.png" alt="" class="self-description" />爱情观</p>
        <p class="desc-detail">{{info.loveMind}}</p>
      </div>
      <div class="desc-item">
        <p class="desc-top"><img src="~@/assets/img/require.png" alt="" class="self-description" />理想的另一半</p>
        <p class="desc-detail">{{info.ideaPartner}}</p>
      </div>
    </div>

    <van-image-preview v-if="info.coverPicUrlList" v-model="isShowPreview" :images="info.coverPicUrlList" :start-position="previewIndex" @change="changePreview"></van-image-preview>
  </div>
</template>

<script>
import { mapState, mapGetters, mapMutations } from 'vuex'
import { Swipe, SwipeItem } from 'vant'

export default {
  name: 'userInfo',
  components: {
    Swipe,
    SwipeItem
  },
  props: {
    info: {
      type: Object,
      default: () => {}
    }
  },
  computed: {
    ...mapState({
      educationMap: 'educationMap',
      salaryMap: 'salaryMap',
      assetsMap: 'assetsMap',
      statusMap: 'statusMap',
      constellationMap: 'constellationMap',
      areaData: 'areaData'
    }),
    ...mapGetters({
      userId: 'userId',
      mId: 'mId'
    }),
    // 所在城市
    cityText (item) {
      return (item) => {
        if (!item.cityId) {
          return ''
        }
        if (parseInt(item.cityId) === 900000) return '现居' + (item.country + ' ' + item.state)
        return `现居${this.areaData['city_list'][item.cityId]}`
      }
    },
    nativeCityText (item) {
      return (item) => {
        if (!item.nativeCityId) {
          return ''
        }
        let nid = parseInt(item.nativeCityId)
        if (nid === 900000) {
          return item.nativeCountry + ' ' + item.nativeState + '人'
        } else {
          const provice = String(nid).substr(0,2) + '0000'
          return `${[110100, 120100, 310100, 500100].indexOf(nid) >= 0 ? '' : `${this.areaData['province_list'][provice]}`}${this.areaData['city_list'][item.nativeCityId]}人`
        }
      }
    },
    schoolText (item) {
      return (item) => `${this.educationMap[item.education]}${item.ifStudent ? '(在读)' : ''}`
    }
  },
  data () {
    return {
      // 图片预览展示标识
      isShowPreview: false,
      previewIndex: 0
    }
  },
  methods: {
    // 计算出生肖
    calculater (year) {
      var arr1 = ['猴', '鸡', '狗', '猪', '鼠', '牛', '虎', '兔', '龙', '蛇', '马', '羊']
      return /^\d{4}$/.test(year) ? `属${arr1[year % 12]}` : false
    },
    execCopy (text) {
      copy(text, this.$refs['code-con']).then(res => {
        this.$toast('复制成功')
      }, () => {
        this.$toast('复制失败')
      })
    },
    showPreview (index) {
      this.previewIndex = index
      this.isShowPreview = true
    },
    changePreview (index) {
      this.$refs['swipe'].swipeTo(index)
    },
    switchReport () {
      this.$emit('switchReport')
    },
    switchFollow () {
      this.$emit('switchFollow')
    }
  }
}
</script>

